*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: sans-serif;
}

.title{
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
}

.home-main{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.heading{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inputs-container{
  display: flex;
  justify-content: center;
  gap: 20px;
}

.input-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.input-group input {
  width: 150px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: center;
  padding: 3px;
}

.btn-group{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.btn{
  padding: 10px;
  border-radius: 5px;
  border: none;
  transition: .5s ease all;
  background-color: #0d6efd;
  color: white;
  font-weight: bold;
}
.btn:hover{
  cursor: pointer;
  background-color: white;
  color: #0d6efd
}